<template>
    <div class="recommend">
        <scroller
                class="season-list-wrap"
                :data="scrollRefreshKey"
        >
            <div>
                <template v-if="computedShortVideoList.length">
                    <more-header title="抖音" v-model="shortVideoMore"></more-header>
                    <season-list :list="computedShortVideoList"></season-list>
                </template>
                <template v-if="computedSeasonList.length">
                    <more-header title="热门动画" v-model="seasonMore"></more-header>
                    <season-list :list="computedSeasonList"></season-list>
                </template>
                <template v-if="computedCnList.length">
                    <more-header title="国产动画" v-model="cnMore"></more-header>
                    <season-list :list="computedCnList"></season-list>
                </template>
            </div>
        </scroller>
        <loading :loading="loading"></loading>
    </div>
</template>

<script type="text/ecmascript-6">
  import {
    getSeasonRecommends,
    getCnRecommends,
    getShortVideos,
    fallbackSeasonRecommends,
    fallbackCnRecommends,
  } from "api/recommend";
  import SeasonList from "components/season-list/season-list";
  import Scroller from "base/scroller/scroller";
  import MoreHeader from "base/more-header/more-header";
  import Loading from "base/loading/loading"
  import {noop} from "common/js/util"

  const INIT_LEN = 4

  export default {
    created() {
      this.getData();
    },
    methods: {
      getData() {
        this._recursionGetList(getSeasonRecommends, "seasonList", fallbackSeasonRecommends);
        this._recursionGetList(getCnRecommends, "cnList", fallbackCnRecommends);
        this._recursionGetList(getShortVideos, "shortVideoList");
      },
      scrollerRefresh() {
        this.scrollRefreshKey = !this.scrollRefreshKey;
      },
      // 接口很辣鸡，总是失败，所以要准备降级请求本地json
      async _recursionGetList(req, listKey, fallback) {
        let data = await req()
        // 请求报错不处理 走回退方案 不catch的话会报错
          .catch(noop)

        if (!data) {
          fallback && (data = await fallback())
        }

        this.list[listKey] = data;
        this.scrollerRefresh();
      }
    },
    data() {
      return {
        seasonMore: false,
        cnMore: false,
        shortVideoMore: false,
        list: {
          seasonList: [],
          cnList: [],
          shortVideoList: [],
        },
        scrollRefreshKey: true
      };
    },
    computed: {
      computedSeasonList() {
        return this.seasonMore
          ? this.list.seasonList
          : this.list.seasonList.slice(0, INIT_LEN);
      },
      computedCnList() {
        return this.cnMore ? this.list.cnList : this.list.cnList.slice(0, INIT_LEN);
      },
      computedShortVideoList() {
        return this.shortVideoMore ? this.list.shortVideoList : this.list.shortVideoList.slice(0, INIT_LEN);

      },
      // 接口巨慢 有一个出来了就不loading了~
      loading() {
        return this.list.seasonList.length === 0 && this.list.cnList.length === 0
      }
    },
    watch: {
      seasonMore() {
        this.scrollerRefresh();
      },
      cnMore() {
        this.scrollerRefresh();
      },
      shortVideoList() {
        this.scrollerRefresh();
      }
    },
    components: {
      SeasonList,
      Scroller,
      MoreHeader,
      Loading
    }
  };
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
    .recommend {
        height: 100%
    }
</style>


